import "./Menu.css";
import React from "react";

function template() {
  return (
    <div className="menu">
      <ul className="menulist" onClick={this.handleMenu}>
        <li id="home" className={this.state.id == "home" && "active"}>
          Home
        </li>
        <li id="about" className={this.state.id == "about" && "active"}>
          About
        </li>
        <li id="contact" className={this.state.id == "contact" && "active"}>
          Contact
        </li>
      </ul>
    </div>
  );
}

export default template;
