import React, { useState } from "react";
const Todo = () => {
  const data = [
    { id: "1", name: "React js", status: "done" },
    { id: "2", name: "React Native", status: "pending" },
    { id: "3", name: "Javascript", status: "pending" },
  ];
  const [todoList, setTodoList] = useState(data);
  const deleteItem = (id) => {
    const deleteData = todoList.filter((ele) => ele.id != id);
    console.log(deleteData);
    setTodoList(deleteData);
  };
  const editItem = (id) => {
    const data = todoList.map((ele) => {
      if (ele.id == id) {
        console.log("inside");
        return { ...ele, status: ele.status == "pending" ? "done" : "pending" };
      }
      return ele;
    });
    setTodoList(data);
  };
  return (
    <div>
      {todoList.map((ele, ind) => {
        return (
          <div
            style={{
              display: "flex",
              justifyContent: "space-around",
              alignItems: "center",
              border: "1px solid green",
              backgroundColor: "pink",
              padding: "10px",
            }}
          >
            <li>{ele.id}</li>
            <li>{ele.name}</li>
            <li style={{ color: ele.status == "done" ? "green" : "red" }}>
              {ele.status}
            </li>
            <button onClick={() => deleteItem(ind + 1)}>delete</button>
            <button onClick={() => editItem(ind + 1)}>edit</button>
          </div>
        );
      })}
    </div>
  );
};

export default Todo;
