import React from "react";
class FilterDemo extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      val: "",
      data: [
        "mobile",
        "car",
        "cloths",
        "entertainment",
        "study",
        "learning",
        "toys",
        "cards",
      ],
    };
  }

  handleData(e) {
    const { value } = e.target;
    const res = this.state.data.filter((ele) => {
      console.log(ele.includes(value));
    });
    console.log("result", res);
    this.setState({ data: res, val: value });
  }
  render() {
    return (
      <div>
        Enter value
        <input
          type="text"
          name="text"
          value={this.state.val}
          onChange={this.handleData.bind(this)}
        />
        {this.state.data.map((ele) => {
          return <li>{ele}</li>;
        })}
      </div>
    );
  }
}
export default FilterDemo;
