import React from "react";
class ListDemo extends React.Component {
  constructor(props) {
    super(props);

    this.state = { val: "", list: [] };
  }
  handleEnter(e) {
    const { value } = e.target;
    if (e.key == "Enter") {
      this.setState({ list: [...this.state.list, value] });
    } else {
      return;
    }
  }
  handleData(e) {
    const { value } = e.target;
    if (e.key == "Enter") {
      return;
    } else {
      if (!this.state.list.includes(value)) {
        this.setState({ val: value });
      } else {
        alert("name present already");
      }
    }
  }
  handleRemovedItem = (eve) => {
    let targetEle = eve.target;

    let classList = targetEle.classList;
    if (classList.contains("item-active")) {
      classList.remove("item-active");
    } else {
      classList.add("item-active");
    }
  };
  render() {
    return (
      <div>
        Enter value
        <input
          type="text"
          name="text"
          value={this.state.val}
          onKeyPress={this.handleEnter.bind(this)}
          onChange={this.handleData.bind(this)}
        />
        {this.state.list.length > 0 &&
          this.state.list.map((ele, ind) => {
            return (
              <li
                style={{ cursor: "pointer" }}
                key={ind}
                onClick={() => this.handleRemovedItem()}
              >
                {ele}
              </li>
            );
          })}
      </div>
    );
  }
}
export default ListDemo;
