import React from "react";
import template from "./Menu.jsx";

class Menu extends React.Component {
  constructor(props) {
    super(props);
    this.state = { id: "home" };
  }
  componentDidMount() {}
  handleMenu = (e) => {
    console.log("clicked", e.target.id);
    this.setState({ id: e.target.id });
  };

  render() {
    return template.call(this);
  }
}

export default Menu;
